*{
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root{
  height: 100vh;
  max-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: nowrap;
  background-color: cornflowerblue;
}
.TodoCounter{
    font-size: 24px;
    color: aliceblue;
    text-align: center;
}
.TodoSearch-Input{
    margin-top: 60px;
    padding-left: 10px;
    width: 350px;
    height: 40px;
    border-radius: 10px;
    border: none;
    max-width: calc(600px - 40px);
    margin-right: 20px;
}

.TodoSearch-Input:focus{
    outline-color: #61AFDA;
}

.TodoSearch-Button{
    width: auto;
    min-width: 40px;
    height: auto;
    min-height: 30px;
    max-height: 40px;
    background-color: azure;
    border: 1px solid transparent;
    border-radius: 10px;
    cursor: pointer;
}


ul{
    padding: 0;
    list-style: none;
}
li{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 60px;
    max-height: 100px;
    width: 420px;
    max-width: calc(600px - 40px);
    padding: 0px 20px;
    margin-top: 10px;
    background-color: azure;
    border-radius: 15px;
}

li p{
    width: auto;
    min-width: 200px;
    max-width: 500px;
}

.Icon--check{
    font-weight: bold;
    cursor: pointer;
}

.Icon-Check--active{
    color: green;
}

.TodoItem-p--complete{
    text-decoration: line-through;
}

.Icon--delete{
    cursor: pointer;
    font-weight: bold;
}

.Icon--delete:hover{
    color: red;
}
.CreateTodo-Title{
    font-size: 24px;
    color: aliceblue;
}

.TodoCreate-Input{
    margin-top: 60px;
    padding-left: 10px;
    width: 300px;
    height: 40px;
    border-radius: 10px;
    border: none;
    max-width: calc(600px - 40px);
    margin-right: 20px;
}

.TodoCreate-Input:focus{
    outline-color: #61AFDA;
}

.TodoCreate-Button{
    position: relative;
    width: auto;
    min-width: 40px;
    height: auto;
    min-height: 30px;
    max-height: 40px;
    background-color: azure;
    border: 1px solid transparent;
    border-radius: 10px;
    cursor: pointer;
    z-index: 1;
}
.Skeleton{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 420px;
    height: 60px;
    margin-top: 10px;
    padding: 0 20px;
    background: white;
    background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
    -webkit-animation-name: preloadAnimation;
            animation-name: preloadAnimation;
    border-radius: 15px;
}

.bar, .bar-2{
    width: 20px;
    height: 10px;
    border-radius: 20px;
    background-color: rgba(128, 128, 128, 0.682);
}

.bar-2{
    width: 200px
}

@-webkit-keyframes preloadAnimation{
    0%{
        background-position: 0px
    }
    100%{
        background-position: 769px
    }
}

@keyframes preloadAnimation{
    0%{
        background-position: 0px
    }
    100%{
        background-position: 769px
    }
}


.ModalBackground {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.TODO-Container, .CreateTODO-Container{
  width: 100%;
  max-width: 600px;
  height: 650px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.CreateTODO-Container{
  max-width: 450px;
  text-align: center;
  border-radius: 20px;
  background-color: purple;
}

